.dialogs {
    display: grid;
    border: solid black 1px;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding-top: 20px;
    padding-left: 20px;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    float: left;
    padding-top: 30px;
    margin-left: 20px;
}

.formAddText {
    width: auto;
    height: auto;
    margin-top: 170px;
    margin-left: 20px;
}